import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min.js"
const Carrr = () => {
  return (
    <>
      <div id="demo" className="carousel slide" data-bs-ride="carousel">
        <ol className="carousel-indicators">
          <li data-bs-target="#demo" data-bs-slide-to="0" className="active"></li>
          <li data-bs-target="#demo" data-bs-slide-to="1"></li>
          <li data-bs-target="#demo" data-bs-slide-to="2"></li>
        </ol>

        <div className="carousel-inner">
          <div className="carousel-item active">
            <img
              src="https://img.freepik.com/fotos-premium/hermoso-entorno-paisaje-campo-verde_29332-1855.jpg?w=2000"
              alt="Los Angeles"
              className="d-block w-100"
            />
          </div>
          <div className="carousel-item">
            <img
              src="https://statics.eleconomista.com.ar/2020/11/614e481503f48.jpg"
              alt="Chicago"
              className="d-block w-100"
            />
          </div>
          <div className="carousel-item">
            <img
              src="https://jugosdelvalle.com.mx/wp-content/uploads/2022/09/wide-angle-shot-single-tree-growing-clouded-sky-during-sunset-surrounded-by-grass-scaled.jpg"
              alt="New York"
              className="d-block w-100"
            />
          </div>
        </div>

        <button
          className="carousel-control-prev"
          type="button"
          data-bs-target="#demo"
          data-bs-slide="prev"
        >
          <span className="carousel-control-prev-icon" aria-hidden="true"></span>
        </button>
        <button
          className="carousel-control-next"
          type="button"
          data-bs-target="#demo"
          data-bs-slide="next"
        >
          <span className="carousel-control-next-icon" aria-hidden="true"></span>
        </button>
      </div>
    </>
  );
}

export default Carrr;
