import React from "react";
import Sinop from "../componentes/infor.jsx";

const Sinops = () =>{

    const datos =  [

        {
 
          "inf" : "python",

        },
 
        {
 
            "inf" : "php",

   
          },        
          {
          "inf" : "php",

 
        },     
    ];
    return(

        <>
         {

           datos.map(({inf}) => {
           
           
             return<Sinop key= {inf} inf={inf} />
           
           
           })}

  
       </>
    )

  

}
export default Sinops;