import React from "react";
import { BrowserRouter as Router , Route, Link,Routes } from 'react-router-dom';

import Menu1 from "./territorio0";
import Menu2 from "./terrenos";
import Menu3 from "./terrenos2";
import Menu4 from "./terrenos3";

const Nave = () => {

  

  
  return (
    <Router>
      <nav style={navStyle}>
        <Link to="/" style={linkStyle}>Home</Link>
        <Link to="/terrenos" style={linkStyle}>About</Link>
        <Link to="/terrenos2" style={linkStyle} >Users</Link>
        <Link to="/terrenos3" style={linkStyle}>Contact Us</Link>
      </nav>
      <Routes>
        <Route path="/" exact element={<Menu1/>} />
        <Route path="/terrenos" element={<Menu2/>} />
        <Route path="/terrenos2" element={<Menu3/>}  />
        <Route path="/terrenos3" element={<Menu4/>} />
      </Routes>
      </Router>
  );


}

const navStyle = {
  background: "#333",
  color: "#fff",
  textAlign: "center",
  padding: "10px"

};

const linkStyle = {
  color: "#fff",
  textDecoration: "none",
  margin: "0 10px"
};

export default Nave;