import React from "react";
import Card from "../componentes/cart";

const Cards = () =>{

    const datos =  [

        {
 
          "lang" : "papa",
          "url" : "https://www.semana.com/resizer/eot1Zv1b5Ysg0BYqJYX0phqByhE=/1280x720/smart/filters:format(jpg):quality(80)/cloudfront-us-east-1.images.arcpublishing.com/semana/UJAFVLTAONCYNATCNNNVONVW3M.jpg",
          "fcolor" : "#ooc3ff",
          "scolor" : "red"
 
        },
 
        {
 
            "lang" : "habichuelas",
            "url" : "https://www.boyaca.gov.co/secretariaagricultura/wp-content/uploads/sites/71/2012/09/images_Productos_Habichuela.jpg",
            "fcolor" : "#ooc3ff",
            "scolor" : "green"
   
          },        
          {
          "lang" : "maiz",
          "url" : "https://delmaiz.info/wp-content/uploads/2017/01/caracter%C3%ADsticas-del-ma%C3%ADz.jpg",
          "fcolor" : "#ooc3ff",
          "scolor" : "green"
 
        },     
    ];
    return(

        <>
         {

           datos.map(({lang,url,fcolor,scolor}) => {
           
           
             return<Card key= {lang} lang={lang} url={url} fcolo={fcolor} scolor={scolor}/>
           
           
           })}

  
       </>
    )

  

}
export default Cards;