document.addEventListener("DOMContentLoaded", function() {
  var botonocultamuestra = document.getElementById("botonocultamuestra");
  var divocultamuestra = document.getElementById("divocultamuestra");

  botonocultamuestra.addEventListener("click", function() {
    var displaying = getComputedStyle(divocultamuestra).display;

    if (displaying === "block") {
      fadeOut(divocultamuestra);
    } else {
      fadeIn(divocultamuestra);
    }
  });

  function fadeIn(element) {
    var opacity = 0;
    element.style.display = "block";

    var fadeInInterval = setInterval(function() {
      if (opacity < 1) {
        opacity += 0.1;
        element.style.opacity = opacity;
      } else {
        clearInterval(fadeInInterval);
      }
    }, 100);
  }

  function fadeOut(element) {
    var opacity = 1;

    var fadeOutInterval = setInterval(function() {
      if (opacity > 0) {
        opacity -= 0.1;
        element.style.opacity = opacity;
      } else {
        clearInterval(fadeOutInterval);
        element.style.display = "none";
      }
    }, 100);
  }
});