import React, { useState } from 'react';

function Pie() {
  const [dato1, setDato1] = useState('');
  const [dato2, setDato2] = useState('');
  const [dato3, setDato3] = useState('');

  const handleChange = (e) => {
    const { id, value } = e.target;
    switch (id) {
      case 'textNam':
        setDato1(value);
        break;
      case 'textAp':
        setDato2(value);
        break;
      case 'textEdad':
        setDato3(value);
        break;
      default:
        break;
    }
  };

  const mostrarDatos = () => {
    const data = {
      dato1: dato1,
      dato2: dato2,
      dato3: dato3,
    };
document.write(
      `<p>nombre: ${data.dato1}</p>` +
      `<p>correo: ${data.dato2}</p>` +
      `<p>telefono: ${data.dato3}</p>`
    );
  };

  return (
    <footer id="diseñoC">
      <h5>Más información</h5>
      <form>
        <label htmlFor="textNam">Correo:</label>
        <input
          type="text"
          id="textNam"
          value={dato1}
          onChange={handleChange}
        />
        <br />

        <label htmlFor="textAp">Nombre:</label>
        <input
          type="text"
          id="textAp"
          value={dato2}
          onChange={handleChange}
        />
        <br />

        <label htmlFor="textEdad">Teléfono:</label>
        <input
          type="number"
          id="textEdad"
          value={dato3}
          onChange={handleChange}
        />
        <br />

        <button type="button" onClick={mostrarDatos}>
          Enviar
        </button>
      </form>
    </footer>
  );
}

export default Pie;