import React, { useState } from 'react';

function Boo() {
  const [mostrarDiv, setMostrarDiv] = useState(false);
  const [dato1, setDato1] = useState('');
  const [dato2, setDato2] = useState('');

  const toggleDiv = () => {
    setMostrarDiv(!mostrarDiv);
  };

  const handleChange = (e) => {
    const { id, value } = e.target;
    switch (id) {
      case 'email':
        setDato1(value);
        break;
      case 'contraseña':
        setDato2(value);
        break;
      default:
        break;
    }
  };

  const mostrarDatos = () => {
    const data = {
      dato1: dato1,
      dato2: dato2,
    };
    
    document.write(
      `<p>email: ${data.dato1}</p>` +
      `<p>contraseña: ${data.dato2}</p>`
    );
  };

  return (
    <div>
      <button
        style={{
          padding: '10px',
          margin: '10px',
          position: 'absolute',
          right: '40px',
          backgroundColor: 'green',
          borderRadius: '20px',
          top: '20px',
        }}
        onClick={toggleDiv}
        value="login"
      >
        Login
      </button>
      {mostrarDiv && (
        <div
          id="no"
          style={{
            backgroundColor: 'greenyellow',
            padding: '10px',
            margin: '10px',
            width: '230px',
            height: '200px',
            right: '30px',
            top: '75px',
            zIndex: 1,
            border: '2px solid green',
          }}
        >
          <form>
            <label htmlFor="email">Email:</label>
            <input
              type="text"
              id="email"
              value={dato1}
              onChange={handleChange}
            />
            <br />
            <label htmlFor="contraseña">Contraseña:</label>
            <input
              type="password"
              id="contraseña"
              value={dato2}
              onChange={handleChange}
            />
            <br />
            <input
              type="button"
              value="entrar"
              onClick={mostrarDatos}
            />
          </form>
        </div>
      )}
    </div>
  );
}

export default Boo;