<div id="pan">
    < img src="https://1.bp.blogspot.com/-Qdmsb6zJGuw/YOx5nSViWbI/AAAAAAAAffA/OsIxAW5Xx30WpOmptqOOFUOi7t5HGhnvgCLcBGAsYHQ/s2048/1.jpg" alt="" width="100%" height="800px"/>
 <h1>zonas</h1>
    <h5>La idea de la creación del SENA, la entidad más querida por los colombianos de acuerdo a diversas encuestas, fue del abogado cartaganero Rodolfo Martínez Tono, quien la plasmó en su tesis de grado profesional, «La Formación Profesional en el Marco de una Política de Empleo». En sus inicios, la entidad se llamó Instituto de Capacitación Obrera, y tuvo como sede una pequeña oficina de la Universidad Nacional. Para captar los primeros aprendices, Martínez Tono se desplazaba a la entrada de las fábricas y se paraba sobre un viejo bus y megáfono en mano invitaba a los trabajadores a formarse. El proyecto vio la luz mediante el Decreto-Ley 118, del 21 de junio de 1957, durante el gobierno de la Junta Militar, tras la renuncia de Gustavo Rojas Pinilla6​. Fue impulsado por el propio Martínez Tono con el apoyo del entonces ministro de Trabajo, Raimundo Emiliani Román. El nombre de la institución se lo puso su fundador, en homenaje al río principal de París (Francia), lugar al que solía visitar. Sus funciones eran brindar formación profesional a los trabajadores, jóvenes y adultos de la industria, el comercio, la agricultura, la minería y la ganadería.7​ La naciente entidad no solo formaba técnicos, sino también empresarios y promovía las pequeñas y medianas empresas.8​9​

Por espacio de tres años desde 1957, la entidad tuvo como sede principal un edificio ubicado en la Calle 20 con Carrera 8. contando inicialmente con el apoyo de las más importantes organizaciones sindicales de Colombia.10​ Mientras que los primeros centros de instrucción fueron aulas del Instituto Técnico Central y de la Universidad Nacional de Colombia, así como improvisadas carpas de circos levantadas en ciudades y municipios, con el fin de ampliar su cobertura, con lo anterior la naciente institución buscaría atender las demandas de la empresa privada, el Gobierno y de los trabajadores, que exigían más capacitación en procura de mejorar su calidad laboral y de vida.

El 23 de agosto de 1957, sesionó por primera vez el Consejo Nacional y se definieron dos tareas a realizar: el estudio, codificación y sistematización de la oferta y demanda de mano de obra, y la capacitación profesional, para poner en práctica estas tareas se establecieron la División de Mano de Obra, la de Formación Profesional y la Administrativa, creada para planificar la construcción y dotación de los centros de formación profesional.

Durante los primeros meses se establecieron las estructuras seccionales en los diversos departamentos como Cundinamarca, Antioquia, Valle del Cauca, Atlántico, Bolívar, Nariño, Cauca y Magdalena. Los departamentos restantes tuvieron sus seccionales al siguiente año.
En 1958, se realizó la primera gran investigación, acerca de las necesidades de formación profesional en todos los departamentos, excepto Chocó y Córdoba, teniendo como objeto de estudio a 5000 empresas de todos los sectores de la industria. El mismo reveló que al menos 210 mil trabajadores requerían complementar su educación y urgía la formación de 25 mil trabajadores adicionales. Con base en lo anterior, se fijó el plan quinquenal 1959-1963.10​

Para llevar a cabo el modelo de capacitación, se diseñaron los programas Promoción Profesional Popular, que llevaron capacitación técnica a diferentes ciudades y poblaciones rurales. En 1960, se firmaron dos importantes convenios con la Organización Internacional del Trabajo y el Fondo Especial de las Naciones Unidas, que brindaron asesoría administrativa, financiera y técnica a los pequeños y medianos empresarios, y promovieron el crecimiento empresarial. Además, la OIT asesoró en la formulación del primer Centro Nacional de Formación de Instructores. En la década de los 60, la entidad tiene un rápido crecimiento con la apertura de diversos centros de formación en todo el territorio nacional, atendiendo los sectores más importantes de la economía: industrial, comercial y agropecuario. En este período, el número de aprendices se incrementó en un 40 % anual, gracias a los aportes del sector privado y a la mejora de la infraestructura y diversificación de oferta.11​</h5></div>
